<template>
    <section class="dark">
        <div class="background">
            <div class="shape"></div>
            <div class="shape"></div>
        </div>
        <form>
            <v-img class="center" src="@/assets/Icon.png" width="200"/>

            <label for="username">{{username}}</label>
            <input type="text" :placeholder="username_placeholder" v-model="form.body.email">

            <label for="password">{{Password}}</label>
            <input type="password" :placeholder="password_placeholder" v-model="form.body.password">

            <!-- <el-select @change="isAllLocations(value_lang)" v-model="value_lang" placeholder="Select" class="mt-3 p-2" style="width:100%">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select> -->
            <v-btn depressed color="0080ff" class="mt-4" @click="loginDefault">{{login}}</v-btn>
        </form>
    </section>
</template>

<script>

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

export default {

    data() {

        return{
            
            title: this.$t('login-section.title'),
            username: this.$t('login-section.username'),
            username_placeholder: this.$t('login-section.username-placeholder'),
            password_placeholder: this.$t('login-section.password-placeholder'),
            Password: this.$t('login-section.Password'),
            remember: this.$t('login-section.remember'),
            login: this.$t('login-section.login'),
            warning: this.$t('login-section.warning'),

            step: 1,
            checked: false,
            options: [{
                value: '1',
                label: 'FranÃ§ais || French'
                }, {
                value: '2',
                label: 'English || Anglais'
                }
            ],
            value_lang: '1',
            form: {
                body: {
                    email: '',
                    password: '',
                    // type:'web'
                },
                remember: false,
                fetchUser: true,
                staySignedIn: false,
                errors: {}
            }
        }
    },

    created(){

    },

    mounted() {
        console.log(this.$auth.user());
    },

    methods: {

        errors(res) {
                this.form.errors = Object.fromEntries(res.data.errors.map(item => [item.field, item.msg]));                
        },


        isAllLocations(params){
            
                if (params == 1) {
                    this.$i18n.locale = 'fr' 
                    console.log(this.$i18n.locale)
                }else{
                    this.$i18n.locale = 'en'
                    console.log(this.$i18n.locale)
                }                  
        },

        loginDefault() {
            NProgress.start();

                this.$auth
                    .login({
                        // body: this.form.body, // VueResource
                        data: this.form.body, // Axios
                        remember: this.form.remember,
                        fetchUser: this.form.fetchUser,
                        staySignedIn: this.form.staySignedIn,
                    }).then(null, (res) => {
                        console.log(res);
                        this.$notify({
                            type: 'error',
                            title: this.warning
                        })
                    }
                    );
            NProgress.done();
        },


    }
}
</script>

<style scoped>
    *,
    *:before,
    *:after{
        padding: 0;
        margin: 0;
        box-sizing: border-box;
        
    }
    section{
        overflow: hidden;
    }
    .dark{
        background: #080710;
        overflow: hidden;
    }
    .background{
        width: 430px;
        height: 520px;
        position: absolute;
        transform: translate(-50%,-50%);
        left: 50%;
        top: 50%;
        
    }
    .background .shape{
        height: 200px;
        width: 200px;
        position: absolute;
        border-radius: 50%;
    }
    .shape:first-child{
        background: linear-gradient(
            #1845ad,
            #23a2f6
        );
        left: -80px;
        top: -80px;
    }
    .shape:last-child{
        background: linear-gradient(
            to right,
            #ff512f,
            #f09819
        );
        right: -30px;
        bottom: -80px;
    }
    .center {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 50%;
    }
    form{
        height: 520px;
        width: 400px;
        background-color: rgba(255,255,255,0.13);
        position: absolute;
        transform: translate(-50%,-50%);
        top: 50%;
        left: 50%;
        border-radius: 10px;
        backdrop-filter: blur(10px);
        border: 2px solid rgba(255,255,255,0.1);
        box-shadow: 0 0 40px rgba(8,7,16,0.6);
        padding: 25px 35px;
    }
    form *{
        font-family: 'Poppins',sans-serif;
        color: #ffffff;
        letter-spacing: 0.5px;
        outline: none;
        border: none;
        align-items: center;
    }
    form h3{
        font-size: 32px;
        font-weight: 500;
        line-height: 42px;
        text-align: center;
    }

    label{
        color: #080710;
        display: block;
        margin-top: 30px;
        font-size: 16px;
        font-weight: 500;
    }
    input{
        display: block;
        height: 50px;
        width: 100%;
        background-color: rgba(7, 7, 7, 0.07);
        border-radius: 3px;
        padding: 0 10px;
        margin-top: 8px;
        font-size: 14px;
        font-weight: 300;
        color: #080710;
    }
    ::placeholder{
        color: #e5e5e5;
    }
    button{
        margin-top: 20px;
        width: 100%;
        padding: 15px 0;
        font-size: 18px;
        font-weight: 600;
        border-radius: 5px;
        cursor: pointer;
    }
</style>